function getFileTypeColor(type: string) {
  switch (type.toLowerCase()) {
    case "pdf":
      return "bg-red-500";
    case "docx":
      return "bg-blue-500";
    case "xlsx":
      return "bg-green-500";
    default:
      return "bg-gray-500";
  }
}

